.warning-card {
  position: absolute;
  background-color: whitesmoke;
  border-radius: 5px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  top: 25px;
  right: -300px;
  min-width: 300px;
  padding: 10px;
  color: rgb(34, 194, 68);
  animation-name: viewWarning;
  animation-duration: 6s;
}

.warning-card > h3 {
  margin: 0;
}

.warning-card > svg {
  font-size: 30px;
  margin-right: 10px;
}

@keyframes viewWarning {
  10% { right: 25px; }
  60% { opacity: 1; }
  100% { right: 25px; opacity: 0; }
}
