@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;600;700&display=swap');

.loginAdminPage {
  background-color: rgb(120, 231, 129);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.loginAdminPage > form {
  background-color: rgb(118, 207, 110);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  border-radius: 5px;
  position: relative;
}

.icon-login {
  width: 100px;
  height: 100px;
  border-radius: 5px;
  background-color: rgb(69, 114, 211);
  margin: 0 auto;
  margin-top: -70px;
  margin-bottom: 20px;
}

.icon-login > img {
  width: 100%;
  filter: invert(100%);
}

.loginAdminPage > form > input {
  border: none;
  border-radius: 2px;
  margin-bottom: 10px;
  font-family: 'Rubik', Arial, sans-serif;
  font-size: 16px;
  padding: 10px;
  width: 300px;
}

.loginAdminPage > form > input:focus {
  outline: none;
}

.loginAdminPage > form > button {
  border: none;
  border-radius: 2px;
  color: whitesmoke;
  cursor: pointer;
  font-family: 'Rubik', Arial, sans-serif;
  font-size: 14px;
  padding: 10px;
  background-color: rgb(69, 114, 211);
}

